import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import LoginForm from '../../../components/login/loginForm';
import LoginRegister from '../../../components/register';
import LoginForgot from '../../../components/forgotten';

import mu from '../../../lib/musher-util';

const initialState = {
	isLoading: false,
    showSignup: false,
};

@observer
class PublicRaceDetailSignup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
        this.updateTimer = {};
    }

	signupToRace = async () => {
		const { raceStore, userStore } = this.props.stores;
		const { user } = userStore;
        const { newSignup } = raceStore;
		const { raceid } = this.props;

        const signup = {
            team: user.team,
            firstname: user.firstname,
            lastname: user.lastname,
            sportsClub: user.sportsClub,
            email: user.email,
            cellphone: user.cellphone,
            facebook: user.facebook,
            instagram: user.instagram,
            ...newSignup
        };

		await raceStore.addContestant({
			id: raceid,
			classId: newSignup.class,
			newContestant: signup,
        });
        raceStore.addSignup(signup);
        this.toggleShowSignup(false, true);
	}

    updateSignup = (e) => {
        const key = e.target.id;
        const value = e.target.value;
        const { raceStore } = this.props.stores;
        raceStore.updateObjectKeyValue('newSignup', key, value);
    }

    editSignup = (e) => {
        const { raceid } = this.props;
        const { raceStore } = this.props.stores;
        const field = e.target.id;
        const value = e.target.value;
        const { classid, contestantid } = e.target.dataset;

        // console.log({ raceid, field, value, classid });
        // const { raceStore } = this.props.stores;
        // raceStore.updateObjectKeyValue('signup', key, value);

        const data = {
			raceClassId: classid,
            contestantId: contestantid,
			[`raceClassContestant${util.ucfirst(field)}`]: value,
		};
		clearTimeout(this.updateTimer[field]);
		this.updateTimer[field] = setTimeout(async () => {
			raceStore.editClass({
				id: raceid,
				data,
			});
		}, 500);
    }

    toggleShowSignup = (e, hide) => {
        const { showSignup } = this.state;
        this.setState({
            showSignup: hide ? false : !showSignup,
        });
    }

	// editRaceClass = async (e) => {
	// 	const { id, field } = e.target.dataset;
	// 	const { value } = e.target;
	// 	const { raceid } = this.props;
	// 	const { raceStore } = this.props.stores;
	// 	const data = {
	// 		raceClassId: id,
	// 		[`raceClass${util.ucfirst(field)}`]: value,
	// 	};
	// 	clearTimeout(this.updateTimer[field]);
	// 	this.updateTimer[field] = setTimeout(async () => {
	// 		raceStore.editClass({
	// 			id: raceid,
	// 			data,
	// 		});
	// 	}, 500);
	// }

	// editSignupToRaceasync = async () => {
	// 	const { raceStore, userStore } = this.props.stores;
	// 	const { user } = userStore;
    //     const { signup } = raceStore;
	// 	const { raceid } = this.props;

	// 	await raceStore.editContestant({
	// 		id: raceid,
	// 		classId: signup.class,
	// 		newContestant: {
	// 			team: user.team,
	// 			firstname: user.firstname,
	// 			lastname: user.lastname,
	// 			sportsClub: user.sportsClub,
	// 			email: user.email,
	// 			cellphone: user.cellphone,
	// 			facebook: user.facebook,
	// 			instagram: user.instagram,
	// 			 ...signup
	// 		} });
	// }

	render() {
        const { showSignup } = this.state;
		const { appState, userStore, raceStore } = this.props.stores;
		const { user = {}, teams = [], isAdmin } = userStore;
        const { darkmode, language = 'en' } = appState;
		const { raceid, subsection } = this.props;
		const { race, signups, newSignup } = raceStore;
		const today = new Date();
        const hasSignedup = signups && signups.length > 0;
        const skipTeams = signups.map(e => parseInt(e.team, 10)) || [];

		return (
			<>
                <h2 class='mb-0'><Text id='races.signup'>Pmelding</Text> <span class='font-weight-lighter' style='letter-spacing: -1px;'> - {race.title}</span></h2>
	            <div class='mb-4'>
                    {race.signupStartDate && <><Text id='races.periode'>Periode</Text>: {util.formatDate(race.signupStartDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
                    {race.signupEndDate && <> - {util.formatDate(race.signupEndDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
                </div>

                {today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate) ? <>

                    {user.email ? <>

                        <span class='font-weight-lighter'><Text id='races.signed-in-as'>Logget inn som</Text>:</span> {user.email}<br /><br />

                        {hasSignedup && signups.map((signup) => {
                            let signupClass = {};
                            if (signup.class) {
                                signupClass = raceStore.getRaceClassById(signup.class);
                            }

                            return (<div class='mb-5 p-3 bg-light border rounded-lg'>
                                <div class='font-weight-bold mb-4'>
                                    <i class='fas fa-check text-success' /> <Text id='races.signed-up'>Din bruker er pmeldt!</Text><br />
                                    {/* {JSON.stringify(signup)} */}
                                </div>

                                {!race.liveRace && race.signuppayment && <>
                                    <div class='form-group row my-0'>
                                        <label for='cellphone' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.signup'>Pmelding</Text></label>
                                        <div class='col-10'>
                                            {signup.paidSignupfee ? <div class='py-2'>
                                                <Text id='races.paid'>Betalt</Text>: {util.formatDate(signup.paidSignupfee)}
                                                <i class='fas fa-check text-success ml-3' />
                                            </div> : <>
                                                <div class='alert alert-danger mb-1' role='alert'>
                                                    <h5>
                                                        <span class='font-weight-lighter'><Text id='races.payment-info'>Betaling pmelding</Text>:</span> {mu.replaceImages(race.signuppayment, race.images, language, false)}<br />
                                                        {signupClass.signupfee || race.signupfee} NOK
                                                    </h5>
                                                </div>
                                            </>}
                                        </div>
                                    </div>
                                </>}

                                <div class='clearfix mt-2'>
                                    <span class='font-weight-lighter float-right'><Text id='races.click-to-change'>Klikk for  endre info i pmeldingen.</Text></span>
                                </div>

                                <div class='form-group row my-0'>
                                    <label for='cellphone' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.cellphone'>Mobil</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='cellphone'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.cellphone}
                                                placeholder={<Text id='races.cellphone-help'>Hva er mobilnummer ditt?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='firstname' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.firstname'>Fornavn</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='firstname'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.firstname}
                                                placeholder={<Text id='races.firstname-help'>Hva er fornavnet ditt?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='lastname' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.lastname'>Etternavn</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='lastname'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.lastname}
                                                placeholder={<Text id='races.lastname-help'>Hva er etternavnet ditt?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='sportsClub' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.sportsclub'>Klubb</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='sportsClub'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.sportsClub}
                                                placeholder={<Text id='races.sportsClub-help'>Hvilken klubb tilhrer du?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='facebook' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.facebook'>Facebook</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='facebook'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.facebook}
                                                placeholder={<Text id='races.facebook-help'>Hva er Facebook brukernavnet ditt?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='instagram' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.instagram'>Instagram</Text></label>
                                    <div class='col-10'>
                                        <Localizer>
                                            <input type='text' class='form-control-plaintext' id='instagram'
                                                data-classid={signup.class}
                                                data-contestantid={signup.id}
                                                onInput={this.editSignup}
                                                value={signup.instagram}
                                                placeholder={<Text id='races.instagram-help'>Hva er Instagram brukernavnet ditt?</Text>}
                                            />
                                        </Localizer>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='team' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.team'>Team</Text></label>
                                    <div class='col-10'>
                                        <select class='form-control-plaintext' id='team' aria-describedby='teamHelp'
                                            data-classid={signup.class}
                                            data-contestantid={signup.id}
                                            onInput={this.editSignup}
                                            value={signup.team}
                                        >
                                            <option value=''>-- <Text id='races.choose-team'>Velg team</Text> --</option>
                                            {teams.map(team => 
                                                <option value={team.id}
                                                    disabled={skipTeams.indexOf(team.id) > -1}
                                                >{team.name} {skipTeams.indexOf(team.id) > -1 && team.id !== signup.team ? '(Already signed up!)' : ''}</option>
                                            )}
                                        </select>
                                    </div>
                                </div>
                                <div class='form-group row my-0'>
                                    <label for='team' class='col-2 col-form-label text-right font-weight-lighter'><Text id='races.class'>Klasse</Text></label>
                                    <div class='col-10'>
                                        <select class='form-control-plaintext' id='class' aria-describedby='classHelp'
                                            onInput={this.editSignup}
                                            value={signup.class}
                                            disabled
                                        >
                                            <option value=''>-- <Text id='races.choose-class'>Velg klasse</Text> --</option>
                                            {race.classes && race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
                                                <option value={currentClass.id}>
                                                    {currentClass.title}{!race.liveRace && <>
                                                        - {currentClass.distance} km - Pmelding: {currentClass.signupfee || race.signupfee} NOK
                                                    </>}
                                                </option>
                                            </>)}
                                        </select>
                                    </div>
                                </div>

                            </div>);
                        })}
                        
                        {race.liveRace === 1 ? <>
                            Signup is done at the race normal web pages.
                        </> : <> 
                            {(hasSignedup && !showSignup) ? <>
                                <button type='button' class='btn btn-sm float-right btn-outline-info' onClick={this.toggleShowSignup}><Text id='races.signup-another-team'>Meld meg p i flere klasser</Text></button>
                            </> : <>

                                <div class='form-group'>
                                    <label for='cellphone'><Text id='races.cellphone'>Mobil</Text></label>
                                    <input type='text' class='form-control' id='cellphone' aria-describedby='cellphoneHelp'
                                        placeholder={user.cellphone}
                                        onInput={this.updateSignup}
                                        value={newSignup.cellphone}
                                    />
                                    <small id='cellphoneHelp' class='form-text text-muted'><Text id='races.cellphone-help'>'Hva er mobilnummer ditt?'</Text></small>
                                </div>

                                <div class='row'>
                                    <div class='col'>
                                        <div class='form-group'>
                                            <label for='firstname'><Text id='races.firstname'>Fornavn</Text></label>
                                            <input type='text' class='form-control' id='firstname' aria-describedby='firstnameHelp'
                                                placeholder={user.firstname}
                                                onInput={this.updateSignup}
                                                value={newSignup.firstname}
                                            />
                                            <small id='firstnameHelp' class='form-text text-muted'><Text id='races.firstname-help'>Hva er fornavnet ditt?</Text></small>
                                        </div>
                                    </div>
                                    <div class='col'>
                                        <div class='form-group'>
                                            <label for='lastname'><Text id='races.lastname'>Etternavn</Text></label>
                                            <input type='text' class='form-control' id='lastname' aria-describedby='lastnameHelp'
                                                placeholder={user.lastname}
                                                onInput={this.updateSignup}
                                                value={newSignup.lastname}
                                            />
                                            <small id='lastnameHelp' class='form-text text-muted'><Text id='races.lastname-help'>Hva er etternavnet ditt?</Text></small>
                                        </div>
                                    </div>
                                </div>

                                <div class='form-group'>
                                    <label for='sportsClub'><Text id='races.sportsclub'>Klubb</Text></label>
                                    <input type='text' class='form-control' id='sportsClub' aria-describedby='sportsClubHelp'
                                        placeholder={user.sportsClub}
                                        onInput={this.updateSignup}
                                        value={newSignup.sportsClub}
                                    />
                                    <small id='sportsClubHelp' class='form-text text-muted'><Text id='races.sportsClub-help'>Hvilken klubb tilhrer du?</Text></small>
                                </div>

                                <div class='row'>
                                    <div class='col'>
                                        <div class='form-group'>
                                            <label for='facebook'><Text id='races.facebook'>Facebook</Text></label>
                                            <input type='text' class='form-control' id='facebook' aria-describedby='facebookHelp'
                                                placeholder={user.facebook}
                                                onInput={this.updateSignup}
                                                value={newSignup.facebook}
                                            />
                                            <small id='facebookHelp' class='form-text text-muted'><Text id='races.facebook-help'>Hva er Facebook brukernavnet ditt?</Text></small>
                                        </div>
                                    </div>
                                    <div class='col'>
                                        <div class='form-group'>
                                            <label for='instagram'><Text id='races.instagram'>Instagram</Text></label>
                                            <input type='text' class='form-control' id='instagram' aria-describedby='instagramHelp'
                                                placeholder={user.instagram}
                                                onInput={this.updateSignup}
                                                value={newSignup.instagram}
                                            />
                                            <small id='instagramHelp' class='form-text text-muted'><Text id='races.instagram-help'>Hva er Instagram brukernavnet ditt?</Text></small>
                                        </div>
                                    </div>
                                </div>
                                <div class='form-group'>
                                    <label for='team'><Text id='races.team'>Team</Text></label>
                                    {!teams || teams.length === 0 && <>
                                        <div class='alert alert-danger' role='alert'>
                                            <Text id='races.no-teams'>Du har ingen team i appen din enn. G inn i appen for  registrer team og forsk igjen.</Text>
                                        </div>
                                    </>}
                                    <select class='form-control' id='team' aria-describedby='teamHelp'
                                        onInput={this.updateSignup}
                                        value={newSignup.team}
                                    >
                                        <option value=''>-- <Text id='races.choose-team'>Velg team</Text> --</option>
                                        {teams.map(team => 
                                            <option value={team.id}
                                                disabled={skipTeams.indexOf(team.id) > -1}
                                            >{team.name} {skipTeams.indexOf(team.id) > -1 ? '(Already signed up!)' : ''}</option>
                                        )}
                                    </select>
                                    <small id='teamHelp' class='form-text text-muted'><Text id='races.choose-team-help'>Velg fra dine team</Text></small>
                                </div>

                                <div class='form-group'>
                                    <label for='class'><Text id='races.class'>Klasse</Text></label>
                                    <select class='form-control' id='class' aria-describedby='classHelp'
                                        onInput={this.updateSignup}
                                        value={newSignup.class}
                                    >
                                        <option value=''>-- <Text id='races.choose-class'>Velg klasse</Text> --</option>
                                        {race.classes && race.classes.map(currentClass => <>
                                            <option value={currentClass.id}>{currentClass.title} - {currentClass.distance} km - Pmelding: {currentClass.signupfee || race.signupfee} NOK</option>
                                        </>)}
                                    </select>
                                    <small id='classHelp' class='form-text text-muted'><Text id='races.choose-class-help'>Velg nsket klasse.</Text></small>
                                </div>

                                {race.rules && <div class='form-group form-check'>
                                    <input type='checkbox' class='form-check-input' id='termsAndAgreement'
                                        onInput={this.updateSignup}
                                        checked={newSignup.termsAndAgreement}
                                        value={true}
                                    />
                                    <label class='form-check-label' for='termsAndAgreement'><Text id='races.agreement-accept'>Jeg har lest og godtar reglene for lpet.</Text></label>
                                </div>}

                                {race.declaration && <div class='form-group form-check'>
                                    <input type='checkbox' class='form-check-input' id='declaration'
                                        onInput={this.updateSignup}
                                        checked={newSignup.declaration}
                                        value={true}
                                    />
                                    <label class='form-check-label' for='declaration'><Text id='races.declaration-accept'>Jeg signerer med dette egenrklringen.</Text></label>
                                </div>}

                                <button type='button' class='btn btn-primary' onClick={this.signupToRace}><Text id='races.do-signup'>Meld meg p</Text></button>

                                {hasSignedup && <button type='button' class='btn btn-danger float-right' onClick={this.toggleShowSignup}><Text id='races.hide-signup'>Avbryt</Text></button>}
                            </>}
                        </>}

                    </> : <>

                        {!subsection && <LoginForm stores={this.props.stores}
                            routeLoginOk={`/race/${raceid}/signup`}
                            routeLoginNew={`/race/${raceid}/signup/newUser`}
                            routeLoginForgot={`/race/${raceid}/signup/forgotPassword`}
                        />}

                        {subsection === 'newUser' && <LoginRegister stores={this.props.stores}
                            routeLogin={`/race/${raceid}/signup`}
                            routeLoginForgot={`/race/${raceid}/signup/forgotPassword`}
                            skipPubSub={true}
                        />}

                        {subsection === 'forgotPassword' && <LoginForgot stores={this.props.stores}
                            routeLogin={`/race/${raceid}/signup`}
                            routeLoginNew={`/race/${raceid}/signup/newUser`}
                        />}
                    </>}

                </> : <>
                    {today < new Date(race.signupStartDate) ? <>
                        <h5 class='mt-4'>
                            <i class='fas fa-exclamation-circle' /> <Text id='races.signup-not-open-yet'>Pmeldingen har ikke pnet enn!</Text>
                        </h5>
                        <p class='mt-4'>
                            <Text id='races.signup-opening'>Pmeldingen pner</Text>:<br />
                            <span class='display-4'>{util.formatDate(race.signupStartDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</span>
                        </p>
                    </> : <>
                        <h5 class='mt-4 mb-4'>
                            <i class='fas fa-exclamation-circle' /> <Text id='races.signup-closed'>Pmeldingen er stengt!</Text>
                        </h5>
                    </>}
                </>}
            </>
		);
	}
}

export default PublicRaceDetailSignup;
